<template>
  <TextArea1
    :autofocus="autofocus"
    v-if="question.type === 'textarea1'"
    :dense="false"
    v-model="answer.value"
    :required="question.required"
    :name="'question-' + question.id"
    placeholder="Enter a description..."
  />
  <TextArea2
    :autofocus="autofocus"
    v-else-if="question.type === 'textarea2'"
    :dense="false"
    v-model="answer.value"
    :required="question.required"
    :name="'question-' + question.id"
    placeholder="Enter a description..."
  />
  <InputTextField
    :autofocus="autofocus"
    v-else-if="question.type === 'textfield'"
    :dense="false"
    v-model="answer.value"
    :name="'question-' + question.id"
    placeholder="Your target audience"
    :required="question.required"
  />
  <SelectOption
    :autofocus="autofocus"
    v-else-if="question.type === 'select'"
    :dense="false"
    :initModel="answer.value"
    v-model="answer.value"
    :selection="question.options"
    :required="question.required"
    :name="'question-' + question.id"
  />
  <SelectOption
    :autofocus="autofocus"
    v-else-if="question.type === 'select_multiple'"
    :dense="false"
    multiple
    :initModel="answer.value"
    v-model="answer.value"
    :selection="question.options"
    :required="question.required"
    :name="'question-' + question.id"
  />
  <RadioSelect
    v-else-if="question.type === 'radio'"
    :dense="false"
    :initModel="answer.value"
    :selection="question.options"
    :required="question.required"
    :name="'question-' + question.id"
    @updated="(v) => (answer.value = v)"
  />
  <CheckboxSelect
    v-else-if="question.type === 'checkbox'"
    :dense="false"
    :initModel="answer.value"
    :selection="question.options"
    :required="question.required"
    :name="'question-' + question.id"
    @updated="(v) => (answer.value = v)"
  />
  <SelectPlatforms
    v-else-if="question.type === 'select_platform'"
    :dense="false"
    :initModel="answer.value"
    :required="question.required"
    :platforms="platforms"
    :name="'question-' + question.id"
    @updated="(v) => (answer.value = v)"
  />
  <InputVideoWalkthrough
    :autofocus="autofocus"
    v-else-if="question.type === 'video_walkthrough'"
    :dense="false"
    class="full-width"
    :name="'question-' + question.id"
    :initModel="answer.value"
    :required="question.required"
    @updated="(v) => (answer.value = v)"
  />
  <SelectBrand
    :autofocus="autofocus"
    v-else-if="question.type === 'select_brand'"
    :dense="false"
    v-model="answer.value"
    :initModel="answer.value"
    :name="'question-' + question.id"
    :required="question.required"
    @brand-added="(e) => (answer.value = e)"
    @brand-finished="(e) => (answer.value = e)"
    @brand-form-closed="
      () => {
        if (answer.value && answer.value?.id === 0) answer.value = '';
      }
    "
  />
  <SelectCategoryType
    :autofocus="autofocus"
    v-else-if="question.type === 'select_taskcategory'"
    :dense="false"
    type="task"
    v-model="answer.value"
    :initModel="answer.value"
    :required="question.required"
  />
  <SelectCategoryType
    :autofocus="autofocus"
    v-else-if="question.type === 'select_projectcategory'"
    :dense="false"
    type="project"
    v-model="answer.value"
    :initModel="answer.value"
    :required="question.required"
  />
  <SelectTaskProjectType
    v-else-if="question.type === 'select_tasktype'"
    :autofocus="false"
    :dense="false"
    type="task"
    v-model="answer.value"
    :initModel="answer.value"
    :required="question.required"
  />
  <SelectTaskProjectType
    v-else-if="question.type === 'select_projecttype'"
    :autofocus="false"
    :dense="false"
    type="project"
    v-model="answer.value"
    :initModel="answer.value"
    :required="question.required"
  />
  <InputWebsite
    :autofocus="autofocus"
    v-else-if="question.type === 'url'"
    :dense="false"
    v-model="answer.value"
    :required="question.required"
  />
  <InputPhoneNumber
    :autofocus="autofocus"
    v-else-if="question.type === 'phone'"
    :dense="false"
    :required="question.required"
    @areaCodeChanged="
      (e) => {
        answer.value = answer.answer
          ? `${e}+${answer.value?.split('+')[1] || ''}`
          : e;
      }
    "
    @numberChanged="
      (e) => {
        answer.value = answer.answer
          ? `${answer.value?.split('+')[0] || ''}+${e}`
          : e;
      }
    "
    :areaCode="answer.value.split('+')[0]"
    :phoneNumber="answer.value.split('+')[1]"
  />
  <FileUploader
    :autofocus="autofocus"
    v-else-if="
      question.type === 'upload_single' || question.type === 'upload_multiple'
    "
    :dense="false"
    :multiple="question.type === 'upload_multiple'"
    temporary
    withItems
    miniItems
    :ref="`upload${question.id}`"
    max-file-size="10000000"
    :note="question?.options?.note || ''"
    :accept="
      question?.options?.accept ||
      'image/*, woff, woff2, otf, ttf, psd, eps, ai, xd, fig'
    "
    :removedFile="uploads[`upload${question.id}`]?.removedFile"
    @added="
      (files) => {
        if (
          !uploads[`upload${question.id}`]?.files?.length ||
          question.type === 'upload_multiple'
        ) {
          addUploadToModel(
            'uploads',
            `upload${question.id}`,
            files,
            `upload${question.id}`
          );
        } else {
          uploads[`upload${question.id}`].files = files;
        }
      }
    "
    @uploaded="
      ({ files, xhr }) => {
        const response = JSON.parse(xhr.response || '{}');
        if (response.success) {
          const data = response.data;
          if (question.type === 'upload_multiple') {
            answer.value = answer.concat(data.map((e) => e.id));
          } else {
            answer.value = data[0]?.id;
          }
        }
      }
    "
    @removed="
      (files) => {
        if (question.type === 'upload_multiple') {
          files
            .map((e) => e.id)
            .forEach((e) => {
              const ind = answer.answer.findIndex((a) => a === e);
              if (ind !== -1) {
                answer.value.splice(ind, 1);
              }
            });
        } else {
          answer.value = '';
        }
      }
    "
    :factory="factoryFn"
    :initSlots="uploads[`upload${question.id}`]"
    thumbnail="auto"
  />
</template>

<script>
import { defineComponent, defineAsyncComponent } from 'vue';
import { DynamicQuestions_Mixin } from 'src/mixins';

export default defineComponent({
  name: 'DynamicQuestions',

  emits: ['answered'],

  mixins: [DynamicQuestions_Mixin],

  components: {
    SelectBrand: defineAsyncComponent(() =>
      import('src/components/Brands/SelectBrand.vue')
    ),
    SelectPlatforms: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectPlatforms.vue')
    ),
    RadioSelect: defineAsyncComponent(() =>
      import('src/components/Helpers/RadioSelect.vue')
    ),
    CheckboxSelect: defineAsyncComponent(() =>
      import('src/components/Helpers/CheckboxSelect.vue')
    ),
    InputTextField: defineAsyncComponent(() =>
      import('src/components/Helpers/InputTextField.vue')
    ),
    TextArea1: defineAsyncComponent(() =>
      import('src/components/Helpers/TextArea1.vue')
    ),
    TextArea2: defineAsyncComponent(() =>
      import('src/components/Helpers/TextArea2.vue')
    ),
    InputVideoWalkthrough: defineAsyncComponent(() =>
      import('src/components/Helpers/InputVideoWalkthrough.vue')
    ),
    SelectOption: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectOption.vue')
    ),
    InputWebsite: defineAsyncComponent(() =>
      import('src/components/Helpers/InputWebsite.vue')
    ),
    InputPhoneNumber: defineAsyncComponent(() =>
      import('src/components/Helpers/InputPhoneNumber.vue')
    ),
    FileUploader: defineAsyncComponent(() =>
      import('src/components/Helpers/FilesUploader.vue')
    ),
    SelectCategoryType: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectCategoryType.vue')
    ),
    SelectTaskProjectType: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectTaskProjectType.vue')
    ),
  },

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    question: {
      type: Object,
      default: () => {},
    },
    initAnswer: {
      type: Object,
      default: () => {},
    },
    platforms: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      answer: {},
    };
  },

  watch: {
    ['answer']: {
      async handler(val) {
        this.$emit('answered', val);
      },
      deep: true,
    },
  },

  async mounted() {
    if (this.initAnswer) this.answer = this.initAnswer;
    this.init();
  },

  methods: {
    async init() {
      if (!this.question?.id) return;
      if (!this.answer?.value)
        this.answer = this.formatDynamicAnswer(this.question);
    },
  },
});
</script>
